form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 13px;
}

button,
a {
  border: none;
  border-radius: 13px;
  cursor: pointer;
  transition: 0.2s;
}

.saveBtn:hover .cvForm {
  border-radius: 0;
}

button:active,
a:active {
  transform: scale(0.9);
}

.cvForm {
  justify-self: center;
  grid-area: cvForm;
}
.cvForm h1,
.cvForm h2 {
  margin: 0;
}
.cvForm header,
.cvForm .personalData,
.cvForm .experience,
.cvForm .skills,
.cvForm .education,
.cvForm .contacts {
  padding: 25px;
  background-color: rgb(28, 28, 28);
  border-radius: 18px;
}
.cvForm header .head {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  gap: 50px;
}
.cvForm header h1 {
  font-size: 1.875rem;
}
.cvForm header .buttonSection {
  display: flex;
  gap: 8px;
}
.cvForm header button,
.cvForm header .createdBy,
.cvForm header a {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.cvForm header button {
  padding: 8px 11px 8px 11px;
  gap: 10px;
  background-color: #f9a01b;
  color: rgb(0, 0, 0);
}
.cvForm header img {
  height: 20px;
}
.cvForm header .createdBy {
  text-align: center;
  margin-top: 25px;
  gap: 8px;
}
.cvForm header a {
  padding: 10px 15px 10px 15px;
  gap: 8px;
  background-color: rgb(36, 36, 36);
  color: white;
  text-decoration: none;
  transition: 0.3s;
}
.cvForm header a:hover {
  background-color: rgb(46, 46, 46);
  color: #f9a01b;
}
.cvForm h2 {
  display: flex;
  align-items: center;
  margin-bottom: 13px;
  gap: 12px;
}
.cvForm img {
  height: 40px;
}
.cvForm .namesSection,
.cvForm .contactsSection {
  display: flex;
  width: 100%;
  gap: 15px;
}
.cvForm input, .cvForm textarea,
.cvForm .newBtn,
.cvForm label {
  background-color: rgb(41, 41, 41);
  color: white;
  border: none;
  border-radius: 13px;
  transition: 0.3s;
}
.cvForm input, .cvForm textarea,
.cvForm label {
  width: 100%;
  height: 40px;
  padding: 10px;
  outline: none;
}
.cvForm input:hover, .cvForm textarea:hover, .cvForm input:focus, .cvForm textarea:focus,
.cvForm label:hover,
.cvForm label:focus {
  background-color: rgb(46, 46, 46);
}
.cvForm label {
  cursor: pointer;
}
.cvForm input[type=file], .cvForm textarea[type=file] {
  display: none;
}
.cvForm textarea {
  height: 150px;
  resize: none;
}
.cvForm h3 {
  margin-top: 30px;
}
.cvForm .experience-quantity {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.cvForm .skill {
  display: flex;
  width: 100%;
  gap: 18px;
}
.cvForm .imgHolder {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  background-color: rgb(36, 36, 36);
  height: 40px;
  width: 55px;
}
.cvForm .imgHolder:hover {
  background-color: rgb(46, 46, 46);
}
.cvForm .imgHolder img {
  height: 35px;
}

.newBtn {
  margin-top: 13px;
  padding: 10px 18px 10px 18px;
}
.newBtn:hover {
  background-color: rgb(46, 46, 46);
  color: #f9a01b;
}

.cvForm > header ~ * {
  margin-top: 25px;
}/*# sourceMappingURL=cv_form.css.map */